





































































import Vue from 'vue';
import {Component} from 'vue-property-decorator';
import {
  defaultExpendTags,
  defaultIncomeTags,
  liveTag,
  amusement,
  cate,
  digit,
  outdoors,
  shopping
} from '@/contants/defaultTag.ts';
import TagList from '@/components/TagList.vue';
type Tag= {
  id:string;
  name:string;
}
@Component({
  components: {TagList}
})
export default class EditTags extends Vue {
  tag:Tag={id:'1',name:'宠物'}
  defaultExpendTags = defaultExpendTags;
  defaultIncomeTags = defaultIncomeTags;
  liveTag = liveTag;
  amusement = amusement;
  cate = cate;
  digit = digit;
  outdoors = outdoors;
  shopping = shopping;

  goBack() {
    this.$router.back();
  }
  resolve(tag:Tag){
    this.$store.commit('createTag',tag)
  }
}
