













import Vue from 'vue';
import {Component,Prop} from 'vue-property-decorator';
type Tag= {
  id:string;
  name:string;
}
@Component
export default class TagList extends Vue {
  @Prop() tagList!: Tag[];
  @Prop() selectedTag!: Tag;

  selected(tag:Tag){
    this.$emit('update:selectedTag',tag)

  }
}
